import type {
  BenchmarkRun,
  ModelPerformance,
  TestResult,
  PerformanceTrend,
  CategoryPerformance
} from '../types/benchmark';

// Import JSONIC database functions
import {
  fetchBenchmarkRunsJsonic,
  fetchModelPerformanceJsonic,
  fetchTestResultsJsonic,
  fetchPerformanceTrendsJsonic,
  fetchCategoryPerformanceJsonic,
  fetchStatsJsonic
} from './jsonicApi';

// Import migration utilities - use Web Worker for mobile compatibility
import { checkAndMigrateWorker } from './workerMigration';
import { jsonicService } from './jsonicService';

// Track initialization
let initialized = false;
let initializationPromise: Promise<boolean> | null = null;

// Progress callback for migration
let migrationProgressCallback: ((progress: any) => void) | undefined;

export function setMigrationProgressCallback(callback: (progress: any) => void) {
  migrationProgressCallback = callback;
}

async function ensureInitialized() {
  if (initialized) return;

  // Ensure we only initialize once
  if (!initializationPromise) {
    initializationPromise = (async () => {
      // Check if we already have data (from main thread migration)
      try {
        await jsonicService.initialize();
        const stats = await jsonicService.getStats();
        if (stats && stats.documentCount > 0) {
          console.log(`[API-JSONIC] Database already initialized with ${stats.documentCount} documents, skipping worker migration`);
          return true;
        }
      } catch (error) {
        console.warn('[API-JSONIC] Failed to check existing data:', error);
      }

      // No data exists, use worker migration
      return checkAndMigrateWorker(migrationProgressCallback);
    })();
  }

  const success = await initializationPromise;
  if (success) {
    initialized = true;
  }
}

export async function fetchBenchmarkRuns(): Promise<BenchmarkRun[]> {
  console.log('[API-JSONIC] fetchBenchmarkRuns called');
  try {
    await ensureInitialized();
    console.log('[API-JSONIC] Fetching benchmark runs from JSONIC...');
    const runs = await fetchBenchmarkRunsJsonic();
    console.log(`[API-JSONIC] Fetched ${runs.length} benchmark runs`);
    return runs;
  } catch (error) {
    console.error('Failed to fetch benchmark runs from JSONIC:', error);
    throw error;
  }
}

export async function fetchModelPerformance(runId?: string): Promise<ModelPerformance[]> {
  try {
    await ensureInitialized();
    return await fetchModelPerformanceJsonic(runId);
  } catch (error) {
    console.error('Failed to fetch model performance from JSONIC:', error);
    throw error;
  }
}

